import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { FormComponent } from '@sofico-framework/ui-kit/components/form';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { EMPTY, fromEvent, merge, Observable, timer } from 'rxjs';
import {
  debounce,
  distinctUntilChanged,
  filter,
  map,
  mapTo,
  tap
} from 'rxjs/operators';

@UntilDestroy()
@Component({
  selector: 'sof-input-file',
  template: `
    <div #dropArea class="input-group" [class.highlight]="highlight$ | async">
      <input
        type="text"
        class="form-control"
        [value]="
          internalValue?.length === 1
            ? internalValue[0]?.name
            : internalValue?.length > 1
            ? (tc + '.FILES-SELECTED-COUNT'
              | translate: { count: internalValue?.length })
            : this.multiple
            ? (tc + '.NO-FILES-SELECTED' | translate)
            : (tc + '.NO-FILE-SELECTED' | translate)
        "
        [attr.id]="labelForId"
        [readOnly]="true"
        [class.is-invalid]="
          invalid ||
          (ngControl?.invalid && (ngControl?.touched || form?.submitted))
        "
        [placeholder]="placeholder"
        (blur)="onTouch()"
        (click)="lockOutOnTouch(); fileInput.click()"
      />
      <div class="input-group-append">
        <button
          #btnElement
          type="button"
          class="btn btn-action"
          [disabled]="isDisabled"
          [class.is-invalid]="
            invalid ||
            (ngControl?.invalid && (ngControl?.touched || form?.submitted))
          "
          (blur)="onTouch()"
          (click)="lockOutOnTouch(); fileInput.click()"
        >
          <sof-svg-icon icon="icon-folder"></sof-svg-icon>
        </button>
      </div>
    </div>
    <input
      #fileInput
      type="file"
      class="d-none"
      [attr.multiple]="multiple"
      [attr.accept]="acceptedMimeTypes"
      [disabled]="isDisabled"
      (input)="onChange($event.target?.files)"
    />
  `,
  styleUrls: ['./input-file.component.scss'],
  providers: [{ provide: SOF_FOCUS_COMPONENT, useExisting: InputFileComponent }]
})
export class InputFileComponent
  implements
    ControlValueAccessor,
    OnInit,
    AfterViewInit,
    OnDestroy,
    OnSofFocus {
  /**
   * Translation context
   */
  @Input() tc: string;

  /**
   * The id of the input to connect to a label tag.
   */
  @Input() labelForId: string;

  /**
   * The placeholder of the input.
   */
  @Input() placeholder = '';

  /**
   *  Determines if the input is disabled.
   */
  @Input() isDisabled: boolean;

  /**
   * Determines whether the input is in a valid state.
   */
  @Input() invalid: boolean;

  /**
   * Allow the dropping file(s).
   */
  @Input() droppable: boolean;

  /**
   * Allow the selection of more than one file.
   */
  @Input() multiple: boolean;

  /**
   * Allowed file types.
   */
  @Input() acceptedMimeTypes: string[] = [
    'application/pdf',
    'image/jpeg',
    'image/x-png'
  ];

  /**
   * Determines the value of the control.
   */
  @Input() set value(value: FileList) {
    this.writeValue(value);
  }

  /**
   * EventEmitter that will emit the value when changed.
   */
  @Output() changeValue = new EventEmitter<FileList>();

  /**
   * EventEmitter that will emit when control is touched.
   */
  @Output() touch = new EventEmitter<any>();

  @ViewChild('dropArea', { static: true })
  dropArea: ElementRef;
  @ViewChild('btnElement') btnElement: ElementRef;

  internalValue: FileList = null;
  propagateChange: any;
  propagateTouch: any;
  dropAreaDropEvent$: Observable<DragEvent>;
  dropAreaDragEnterEvent$: Observable<DragEvent>;
  dropAreaDragOverEvent$: Observable<DragEvent>;
  dropAreaDragLeaveEvent$: Observable<DragEvent>;
  highlight$: Observable<boolean>;
  droppedFiles$: Observable<FileList>;
  private flagPositiveClick: boolean;

  constructor(
    @Optional() public form: FormComponent,
    @Optional() @Host() public ngControl: NgControl
  ) {
    if (ngControl) {
      ngControl.valueAccessor = this;
    }
  }

  sofFocus(): void {
    this.btnElement.nativeElement.focus();
  }

  ngOnInit(): void {
    this.dropAreaDropEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'drop'
    );
    this.dropAreaDragEnterEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'dragenter'
    );
    this.dropAreaDragOverEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'dragover'
    );
    this.dropAreaDragLeaveEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'dragleave'
    );

    this.highlight$ = this.getHighlight$();

    this.droppedFiles$ = this.getDroppedFiles$();
  }

  ngAfterViewInit(): void {
    this.droppedFiles$.pipe(takeUntilDestroy(this)).subscribe(fileList => {
      this.onChange(fileList);
    });
  }

  ngOnDestroy(): void {
    if (this.ngControl?.valueAccessor) {
      // Every time a control is re-created the previous writeValue reference(s) is not cleaned up.
      // So, over time, a lot of these references can be built up. This memory leak is a bug in Angular's implementation of ControlValueAccessor.
      // We hide that problem by assigning an empty function to writeValue every time we destroy the control.
      // An detailed explanation of the problem can be found here: https://github.com/angular/angular/pull/29335
      // The bug issue for it: https://github.com/angular/angular/issues/20007
      this.ngControl.valueAccessor.writeValue = () => {};
    }
  }

  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.propagateTouch = fn;
  }

  writeValue(value: FileList): void {
    this.internalValue = value ?? null;
  }

  onChange(value: FileList): void {
    if (!this.isDisabled && value) {
      const newInternalValue = value ?? null;

      // emit value
      this.changeValue.emit(newInternalValue);

      // propagate the change
      if (this.propagateChange) {
        this.internalValue = newInternalValue;
        this.propagateChange(newInternalValue);
      }
    }
  }

  onTouch(): void {
    if (this.flagPositiveClick) {
      this.flagPositiveClick = false;
    } else {
      this.touch.emit();

      if (!this.isDisabled && this.propagateTouch) {
        this.propagateTouch();
      }
    }
  }

  setDisabledState(value: boolean): void {
    this.isDisabled = value;
  }

  lockOutOnTouch(): void {
    this.flagPositiveClick = true;
  }

  private getDroppedFiles$(): Observable<FileList> {
    return this.dropAreaDropEvent$.pipe(
      map((e: DragEvent) => e.dataTransfer.files),
      filter(files => (this.multiple ? files.length > 0 : files.length === 1)),
      filter(files =>
        Array.from(files).every(file =>
          this.acceptedMimeTypes.includes(file.type)
        )
      )
    );
  }

  private getHighlight$(): Observable<boolean> {
    return merge(
      merge(this.dropAreaDragEnterEvent$, this.dropAreaDragOverEvent$).pipe(
        mapTo(true)
      ),
      merge(this.dropAreaDropEvent$, this.dropAreaDragLeaveEvent$).pipe(
        mapTo(false)
      )
    ).pipe(
      // This delays un-highlighting the drop zone,
      // first added as a fix to eliminate flickering by dragLeave Events
      distinctUntilChanged(),
      debounce(startDevice => (startDevice ? EMPTY : timer(100))),
      distinctUntilChanged()
    );
  }

  private getDropEvent$(
    elementRef: any,
    eventName:
      | 'drag'
      | 'dragend'
      | 'dragenter'
      | 'dragexit'
      | 'dragleave'
      | 'dragover'
      | 'dragstart'
      | 'drop'
  ): Observable<DragEvent> {
    return fromEvent(elementRef, eventName).pipe(
      tap((e: Event) => {
        e.preventDefault();
        e.stopPropagation();
      })
    ) as Observable<DragEvent>;
  }
}
